
import {defineComponent} from "vue";
import Modal from "@/components/common/Modal.vue";
import moment, {Moment} from "moment";
import DatePicker from "@/components/common/DatePicker.vue";
import Remote from '@/remote/scheduleRequest'
import Dialog from "@/components/dialog";

export enum CopyOptions {
    copy = 'replace',
    merge = 'merge'
}

export default defineComponent({
    name: "DraftCreationDialog",
    components: {
        Modal,
        DatePicker
    },
    data() {
        return {
            datePickerTop: 0,
            datePickerLeft: 0,
            sourceMode: 'copy',
            copyOption: 'replace',

            //
            scheduleStartDateText: '',
            copyFromDateText: '',
            targetDateText: '',

            //
            scheduleStartDate: moment(),
            copyFromDate: moment(),
            copyToDateDuration: 0,
            targetDate: moment(),

            //
            dateTarget: '',
            copyPersonalDuration: 0
        }
    },
    mounted() {
        // this.toggleShow();
    },
    methods: {
        toggleShow() {
            (this.$refs.ModalRef as any).toggleShow();
        },

        actionByMode() {
            if (this.sourceMode === 'copy') {
                this.createDraft();
            } else {
                this.importFromPersonal();
            }
        },

        importFromPersonal() {
            Dialog.loading()
            let data = {
                CopyFromPersonalSchedule: true,
                EmployeeNumbers:  this.$store.state.EmployeeModule.employees.map((employee: any) => employee.employeeNo),
                PersonalSchedCopyDurationInDays: this.copyPersonalDuration,
                TargetDate: this.scheduleStartDate.format('DD/MMM/YYYY'),
                CopyOption: this.copyOption
            }

            Remote.CopyFlexSchedule(data)
            .then(res => {
                (this.$refs.ModalRef as any).toggleShow();
                this.$emit('reload');
            })
            .finally(() => {
                // Dialog.clear();
            })
        },

        createDraft() {
            Dialog.loading()
            console.log('copyToDateDuration', this.copyToDateDuration)
            let data = {
                CopyFromPersonalSchedule: false,
                EmployeeNumbers: this.$store.state.EmployeeModule.employees.map((employee: any) => employee.employeeNo),

                CopyFromDate: this.copyFromDate.format('DD/MMM/YYYY'),
                CopyToDate: this.copyFromDate.add(this.copyToDateDuration, 'days').format('DD/MMM/YYYY'),
                TargetDate: this.targetDate.format('DD/MMM/YYYY'),
                CopyOption: this.copyOption
            }
            Remote.CopyFlexSchedule(data)
            .then(res => {
                (this.$refs.ModalRef as any).toggleShow();
                this.$emit('reload');
            })
            .finally(() => {
                // Dialog.clear();
            })
        },

        async updateDateTime($event: any, selectedDateTarget: string) {
            console.log('event', $event)
            console.log('event', selectedDateTarget)
            let element = $event.target.getClientRects()[0]
            this.datePickerTop = element.bottom;
            this.datePickerLeft = element.left;

            try {
                switch (selectedDateTarget) {
                    case 'ScheduleStartDate' :
                        this.scheduleStartDate = await (this.$refs.DatePickerRef as any).toggleShow(this.scheduleStartDate);
                        this.scheduleStartDateText = this.scheduleStartDate.format('DD/MM/YYYY')
                        return;
                    case 'copyFromDate' :
                        this.copyFromDate = await (this.$refs.DatePickerRef as any).toggleShow(this.copyFromDate);
                        this.copyFromDateText = this.copyFromDate.format('DD/MM/YYYY')
                        return;
                    case 'targetDate' :
                        this.targetDate = await (this.$refs.DatePickerRef as any).toggleShow(this.targetDate);
                        this.targetDateText = this.targetDate.format('DD/MM/YYYY')
                        return;

                }
            } catch (e) {
                return
            }
        },
        cancel() {
            this.toggleShow();
        }

    }
})
